import React, { useContext, useRef, useState } from 'react'
import './contact.css'
import Phone from '../../img/phone.png'
import Email from '../../img/email.png'
import Address from '../../img/address.png'
import emailjs from 'emailjs-com';
import { ThemeContext } from '../../context'

const Contact = () => {
    const formRef = useRef()

    const [done, setDone] = useState(false)

    const theme = useContext(ThemeContext)

    const darkMode = theme.state.darkMode

    const handleSubmit = (e) => {
        e.preventDefault()
        emailjs.sendForm('service_cy5a4u3', 'template_j18vsgl', formRef.current, 'user_bqjjU8sx3EiGGuEUSzgkg')
            .then((result) => {
                console.log(result.text);
                setDone(true)
            }, (error) => {
                console.log(error.text);
            });
    }
    return (
        <div className="c">
            <div className="c-bg"></div>
            <div className="c-wrapper">
                <div className="c-left">
                    <h1 className="c-title">Let's discuss your project</h1>
                    <div className="c-info">
                        <div className="c-infor-item">
                            <img src={Phone} alt="" className="c-icon" />
                            +84 869 315 579
                        </div>
                        <div className="c-infor-item">
                            <img src={Email} alt="" className="c-icon" />
                            nguyenthanhrin26@gmail.com
                        </div>
                        <div className="c-infor-item">
                            <img src={Address} alt="" className="c-icon" />
                            Le Van Tho Street, Ward 13, Go Vap Distric, Ho Chi Minh City
                        </div>
                    </div>
                </div>
                <div className="c-right">

                    <p className="c-desc">

                    </p>
                    <form ref={formRef} onSubmit={handleSubmit}>
                        <input style={{ backgroundColor: darkMode && "#999" }} type="text" placeholder="  Name" name="user_name" />
                        <input style={{ backgroundColor: darkMode && "#999" }} type="text" placeholder="  Subject" name="user_subject" />
                        <input style={{ backgroundColor: darkMode && "#999" }} type="text" placeholder="  Email" name="user_email" />
                        <textarea style={{ backgroundColor: darkMode && "#999" }} name="message" placeholder="  Message" rows="5"></textarea>
                        <button type="submit">Submit</button>
                        {done && "Thank you!"}
                    </form>
                </div>
            </div>
        </div>
    )
}

export default Contact
