import "./intro.css";
import avatar from "../../img/avatar.png";

const Intro = () => {
    return (
        <div className="i">
            <div className="i-left">
                <div className="i-left-wrapper">
                    <h2 className="i-i">Hello, My name is</h2>
                    <h1 className="i-name">Rin Nguyen</h1>
                    <div className="i-title">
                        <div className="i-wrapper">
                            <div className="i-title-item">Web Developer</div>
                            <div className="i-title-item">Designer</div>

                        </div>
                    </div>
                    <p className="i-desc">
                        II am looking for a job that matches my ability and a professional working environment to develop myself, learn a lot of useful things, improve my qualifications so that I can contribute my best to your company.
                    </p>

                </div>
            </div>
            <div className="i-right">
                <div className="i-bg"></div>
                <img src={avatar} alt="" className="i-img" />

            </div>
        </div>
    )
}

export default Intro
